<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" @click="goBack"></a>
      <h1 class="mui-title">{{title}}</h1>
    </header>
  </div>
</template>

<script>
    export default {
      name: "navBar",
      props:['title'],//申明接收父组件参数
      methods:{
        goBack(){
          this.$router.go(-1);//根据浏览记录返回上一页
        }
      }
    }
</script>

<style scoped>
  .mui-bar.mui-bar-nav{
    position: relative;
  }
</style>



