// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
//Mint：引入css
import 'mint-ui/lib/style.css'
//Mint:按需引入mint-ui
import Header from 'mint-ui/lib/header';
import Switch from 'mint-ui/lib/switch';
import Swipe from 'mint-ui/lib/swipe';
import SwipeItem from 'mint-ui/lib/swipe-item';
import Lazyload from 'mint-ui/lib/lazyload';
import Loadmore from 'mint-ui/lib/loadmore';
import Indicator from 'mint-ui/lib/indicator';
import Button from 'mint-ui/lib/button';
import Field from 'mint-ui/lib/field';
import Cell from 'mint-ui/lib/cell';


Vue.component(Header.name,Header);
Vue.component(Switch.name,Switch);
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);
Vue.component(Loadmore.name,Loadmore);
Vue.component(Button.name,Button);
Vue.component(Field.name, Field);
Vue.component(Cell.name, Cell);
Vue.use(Lazyload);

//Axios:引入
import Axios from 'axios'
//Axios:挂载属性
Vue.prototype.$axios = Axios
//Axios:全局拦截器loadding
//挂在host
Vue.prototype.$host = 'http://vuecms.bigfool.cn'
Axios.interceptors.request.use(function (config) {
  //显示图标
  Indicator.open({
    text:'加载中...',
    spinnerType:'fading-circle'
  });
  return config;
})
Axios.interceptors.response.use(function (config) {
  //关闭图标
  Indicator.close();
  return config;
})

//MUI:引入mui.css
import '../static/vender/mui/dist/css/mui.css'
//引入自定义样式global.css
import  '../static/css/global.css'
//Moment：引入
import Moment from 'moment'
//引入全局组件所需组件对象
import NavBar from './components/common/NavBar'
import Comment from './components/common/Comment'
// 定义全局过滤器和组件
Vue.filter('filterTime',function (value) {
  return Moment(value).format('YYYY-MM-DD');
})
Vue.component('navBar',NavBar)  //标题全局组件
Vue.component('comment',Comment)  //评论全局组件

import App from './App'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



