import Vue from 'vue'
import Router from 'vue-router'
//按需加载路由
const Home = resolve => require([( '../components/home/Home')],resolve)
const Member = resolve => require([( '../components/member/Member')],resolve)
const ShopCart = resolve => require([( '../components/shopcart/ShopCart')],resolve)
const Search = resolve => require([( '../components/search/Search')],resolve)
const NewsList = resolve => require([( '../components/news/NewsList')],resolve)
const NewsDetail = resolve => require([( '../components/news/NewsDetail')],resolve)
const PhotoShare = resolve => require([( '../components/photo/PhotoShare')],resolve)
const PhotoDetail = resolve => require([( '../components/photo/PhotoDetail')],resolve)
const GoodsList = resolve => require([( '../components/goods/GoodsList')],resolve)
const GoodsDetail = resolve => require([( '../components/goods/GoodsDetail')],resolve)
const GoodsComment = resolve => require([( '../components/goods/GoodsComment')],resolve)
const GoodsInfo = resolve => require([( '../components/goods/GoodsInfo')],resolve)
const Login = resolve => require([( '../components/member/Login')],resolve)
const Register = resolve => require([( '../components/member/Register')],resolve)
const UserInfo = resolve => require([( '../components/member/UserInfo')],resolve)

Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass:'mui-active',//点击router-link后添加自定义样式
  routes: [
    {path: '/', redirect:{name:'home'}},//跳转到首页
    {name:'home',path:'/home',component:Home},//首页
    {name:'login',path:'/login',component:Login},//登陆
    {name:'register',path:'/register',component:Register},//登陆
    {name:'member',path:'/member',component:Member},//会员
    {name:'userinfo',path:'/userinfo',component:UserInfo},//用户资料
    {name:'shopcart',path:'/shopcart',component:ShopCart},//购物车
    {name:'search',path:'/search',component:Search},//查找
    {name:'news.list',path:'/news/list',component:NewsList},//新闻资讯
    {name:'news.detail',path:'/news/detail',component:NewsDetail},//新闻详情
    {name:'photo.share',path:'/photo/share',component:PhotoShare},//图文分享
    {name:'photo.detail',path:'/photo/detail/:id',component:PhotoDetail},//图文详情
    {name:'goods.list',path:'/goods/list',component:GoodsList},//商品列表
    {name:'goods.detail',path:'/goods/detail',component:GoodsDetail},//商品详情
    {name:'goods.comment',path:'/goods/comment',component:GoodsComment},//商品评论
    {name:'goods.info',path:'/goods/info',component:GoodsInfo},//商品评论

  ]
})



