<template>
    <div>
      <!--评论-->
      <div class="photo-bottom">
        <ul>
          <li class="photo-comment">
            <div>
              <span>提交评论</span>
              <span><a @click="goBack()">返回</a></span>
            </div>
          </li>
          <li class="txt-comment">
            <textarea v-model="msg"></textarea>
          </li>
          <li>
            <mt-button type="primary" size="large" @click="sendComment()">发表评论</mt-button>
          </li>
          <li class="photo-comment">
            <div>
              <span>评论列表</span>
              <span>44条评论</span>
            </div>
          </li>
        </ul>
        <ul class="comment-list">
          <li v-for="comment in comments" :key="comment.comment_id">
            {{comment.author}}：{{comment.content}}<span style="float: right">{{comment.add_time|filterTime}}</span>
          </li>
        </ul>
        <mt-button type="danger" size="large" plain @click="loadMore()">加载更多</mt-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "Comment",
      props:['category','cid'],
      data(){
        return{
          comments:[],//评论数据
          pageIndex:1,//页码
          msg:'',//发表内容
        }
      },
      created(){
        //初始化加载最新评论数据
        this.loadFirst();
      },
      methods:{
        //加载最新一页评论数据
        loadFirst(){
          this.$axios.get(this.$host+'/api/getcomment/'+this.category+"/"+this.cid+'?page=1')
            .then(res=>{
              // console.log(res.data.data.data);
              this.comments = res.data.data.data;
            })
            .catch(err=>{
              console.log(err);
          })
        },
        //加载更多
        loadMore(){
          this.pageIndex = this.pageIndex + 1;
          this.$axios.get(this.$host+'/api/getcomment/'+this.category+"/"+this.cid+'?page='+this.pageIndex)
            .then(res=>{
              console.log(res.data.data.data);
              this.comments = this.comments.concat(res.data.data.data);
            })
            .catch(err=>{
              console.log(err);
            })
        },
        //发表评评论
        sendComment(){
          this.$axios.post(this.$host+'/api/postcomment/'+this.category+"/"+this.cid,'content='+this.msg)
            .then(res=>{
              this.msg = '';
              this.loadFirst();
            })
            .catch(err=>{
              console.log(err);
            })
        },
        //返回
        goBack(){
          this.$router.go(-1);
        }
      }
    }
</script>

<style scoped>
  .photo-comment > div span:nth-child(1){
    float:left;
    font-weight: bold;
    margin-left: 5px;
  }
  .photo-comment > div span:nth-child(2){
    float:right;
  }
  .photo-comment{
    height: 30px;
    border-bottom: 1px solid rgba(0,0,0,0.4);
    line-height: 30px;
    margin-bottom: 5px;
  }
  .txt-comment{
    padding: 5px 5px;
  }
  .txt-comment textarea{
    margin-bottom: 5px;
  }
  .comment-list{
    border-bottom: 1px solid rgba(0,0,0,0.2);
    padding-bottom: 5px;
    margin-bottom: 5px;
    padding-left: 5px;
  }
  ul{
    margin: 0;
    padding: 0;
  }
  li{
    list-style: none;
  }
</style>



